export default ".switch {\n    position: relative;\n    display: inline-block;\n    width: 60px;\n    height: 34px;\n  }\n  \n  .switch input {display:none;}\n  \n  .slider {\n    position: absolute;\n    cursor: pointer;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    background-color: #ccc;\n    transition: .4s;\n  }\n  \n  .slider:before {\n    position: absolute;\n    content: \"\";\n    height: 26px;\n    width: 26px;\n    left: 4px;\n    bottom: 4px;\n    background-color: white;\n    transition: .4s;\n  }\n  \n  input:checked + .slider {\n    background-color: rgb(33, 243, 145);\n  }\n  \n  input:focus + .slider {\n    box-shadow: 0 0 1px rgb(33, 243, 145);\n  }\n  \n  input:checked + .slider:before {\n    transform: translateX(26px);\n  }\n  \n  /* Rounded sliders */\n  \n  .slider.round {\n    border-radius: 34px;\n  }\n  \n  .slider.round:before {\n    border-radius: 50%;\n  }\n  \n/*# sourceMappingURL=data:application/json;base64, */"